import React, { useState } from 'react';
import './App.css';

function App() {

const [values, setValues] = useState({
  firstName: '',
  lastName: '',
  email: ''
})

const [valid, setValid] = useState(false)
 
const setLocalStorage = items => {
  localStorage.setItem('form data', JSON.stringify(items))
}

const [submitted, setSubmitted] = useState(false)

const handleChangeFirst = e => {
  const firstData = {...values, firstName:e.target.value} 
  setValues(firstData)
  setLocalStorage(firstData)
}

const handleChangeLast = e => {
  const firstData = {...values, lastName:e.target.value}
  setValues(firstData)
  setLocalStorage(firstData)
}

const handleSubmit = e => {
e.preventDefault();
if (values.firstName && values.lastName) {
  setValid(true)
}
  setSubmitted(true)
}

  return (
  <React.Fragment>
    <div className="app">
      { valid ? <div className="thank">Thank you for registering</div> : null}
    <h1>React Form</h1>
    <form onSubmit={handleSubmit}>
          <div>
          <input 
          className="input" 
          onChange={handleChangeFirst}
          value={values.firstname} 
          placeholder="First Name"
          name="firstname" />
        </div>

        <div>
          <input 
          className="input" 
          onChange={handleChangeLast}
          value={values.lastName} 
          placeholder="Last Name"
          name="lastname" />
        </div>
      
     
    <button className="btn">Submit</button>
    </form>
    </div>
  </React.Fragment>
    )
}

export default App;
